import React from 'react'

function Apresentação() {
    return (
        <div id='presentaion'>
            <div className='pr-heading'>
                <h1>Quem somos?</h1>
                <h2>Nascemos com o propósito de transformar o mundo por meio da inovação.</h2>
                <p className='details'>Fazemos isso apoiando grandes empresas a usarem todos seus recursos e alcance para melhorar a vida de seus clientes, funcionários e parceiros de forma social e ambientalmente consciente, sem perder o foco na geração de resultado.</p>
                <div className='pr-btns'>
                    <a href='#about'  className='pr-btn'>Saiba mais</a>
                </div>
            </div>
        </div>
    )
}

export default Apresentação
