import React from 'react';
import ServiçosBox from './ServiçosBox';
import fimage1 from '../images/1.svg';
import fimage2 from '../images/2.svg';
import fimage3 from '../images/3.svg';
import fimage4 from '../images/4.svg';


function Serviços() {
    return (
        <div id='features'>
            <h1>SERVIÇOS</h1>
            <div className='a-container'>
                
                    <ServiçosBox image={fimage1} title='Desenvolvimento de sites' descr='Crie um site como esse para sua empresa.'/>
                    <ServiçosBox image={fimage2} title='Desenvolvimento de Aplicativos' descr='Desenvolvemos aplicações Android e IOS.'/>
                    <ServiçosBox image={fimage3} title='Gravações e transmissão de lives'descr='Fazemos transmissões dos seus eventos.'/>
                    <ServiçosBox image={fimage4} title='Criação de mídias digitais.' descr='Criamos artes digitais com a sua cara.'/>

            </div>
        </div>
    )
}

export default Serviços;
