import React from 'react'

function Header() {
    return (
        <div id='main'>
            <div className='name'>
                <h1>UMA NOVA <span>IDEIA</span></h1>
                <h1>PARA SUA <span>EMPRESA!</span></h1>
                <p className='details'>Somos espacialistas em pensar fora da caixa!</p>
                <div className='header-btns'>
                    <a href='#contact' className='header-btn' smooth={true} duration={1000}>CONTATO</a>
                </div>
            </div>
        </div>
    )
}

export default Header
